.nav {
  display: flex;

  &__list {
    display: flex;
  }

  &__link {
    padding: 22px 0;
    display: block;
    text-decoration: none;
    color: $text-color-main;
    font-size: 12px;
    font-weight: 500;
    text-transform: uppercase;
    line-height: 16px;
    position: relative;

    &:hover {
      color: $color-blue;
    }

    &--is-active {
      color: $color-blue;
    }

    &--is-active::after {
      content: "";
      display: block;
      background-color: $color-blue;
      border-radius: 8px;
      position: absolute;
      width: 100%;
      height: 4px;
      bottom: 0;
    }
  }



  &__item {
    list-style: none;
    margin-left: 20px;
  }

  &__item:first-child {
    margin-left: 0;
  }
}
